<template>
    <div>
       <div class="nav">
           <div class="content">
                <div class="left" ref="left">
                <ul>
                    <li :class="{current:i == currentIndex}" v-for="(item,i) in left" :key="i" @click="selectItem(i,$event)">
                        <span class="left-item">{{ item }}</span>
                    </li>
                   
                </ul>
            </div>
            <div class="right" ref="right">
                <ul>
                    <li v-for="(item,i) in right" :key="i" class="right-item right-item-hook">
                        <h2>{{ item.name }}</h2>
                        <ul class="clearfix">
                            <router-link to="/details" tag="li" v-for="(num,j) in item.content" :key="j">
                                <div class="imgli">
                                   <img :src="item.img" alt="">
                                </div>
                                <p>{{num}}</p>
                                </router-link>
                        </ul>
                    </li>
                </ul>
            </div>
           </div>
       </div>
    </div>
</template>

<script>
import BScroll from '@better-scroll/core'
    export default {
        data:function(){
            return {
                left:["","","","","","","",""],
               right:[
                    {name:"",content:["","","",""],img:[require("../../assets/imgs/class_phone.png")]},
                    {name:"",content:["","","","",""],img:[require("../../assets/imgs/class_tv.jpg")]},
                    {name:"",content:["","","","",""],img:[require("../../assets/imgs/good1.webp")]},
                    {name:"",content:["","","","",""],img:[require("../../assets/imgs/good2.jpg")]},
                    {name:"",content:["","","","",""],img:[require("../../assets/imgs/good3.jpg")]},
                    {name:"",content:["","","","",""],img:[require("../../assets/imgs/goodshot.jpg")]},
                    {name:"",content:["","","","",""],img:[require("../../assets/imgs/details_1.jpg")]},
                    {name:"",content:["","","","",""],img:[require("../../assets/imgs/details_2.jpg")]},
                ],
                scrollY:0,
                listHeight:[],  //li
            }
        },
        methods: {
                _initScroll(){
                    // 
                    this.lefts = new BScroll(this.$refs.left,{
                        click:true, //
                        probeType:3 //
                    });
                    this.rights = new BScroll(this.$refs.right,{
                        click:true, //
                        probeType:3 //
                    });

                    // right
                    this.rights.on("scroll",(pos)=>{
                        this.scrollY = Math.abs(Math.round(pos.y));
                    })
                },
                _getHeight(){
                    let rightItems = this.$refs.right.getElementsByClassName("right-item-hook");
                    let height = 0;//
                    this.listHeight.push(height); //
                    // 
                    for(let i = 0;i < rightItems.length;i++){
                        let item = rightItems[i];
                        height += item.clientHeight; // 
                        this.listHeight.push(height);
                    }
                },
                // 
                // index 
                // event 
                selectItem(index,event){
                    // better-scrolleventevent_constructed
                    // _constructedreturn
                    if(!event._constructed){
                        return;
                    }else{
                        // 
                        let rightItems = this.$refs.right.getElementsByClassName("right-item-hook");
                        let el = rightItems[index];
                        // scrollToElement()//
                        this.rights.scrollToElement(el);

                        // 
                        let leftItems = this.$refs.left.getElementsByTagName("li");
                        let elLi = leftItems[index];
                        this.lefts.scrollToElement(elLi);
                    }
                }
            },
            // 
            mounted() {  //
                // html
                // 
                // $nextTick() DOM
                this.$nextTick(()=>{
                    this._initScroll();//
                    this._getHeight(); //

                    // 
                    if("id" in this.$route.query){
                        // 
                        let rightItems = this.$refs.right.getElementsByClassName("right-item-hook");
                        let el = rightItems[this.$route.query.id];
                        this.rights.scrollToElement(el);
                    }
                })
            },
            // 
            computed: {
                currentIndex(){
                    for(let i = 0; i < this.listHeight.length;i++){
                        let height = this.listHeight[i];
                        let height2 = this.listHeight[i+1];
                        // height2heightheight2
                        if(!height2 || (this.scrollY >=height && this.scrollY < height2) ){
                        
                            // 
                            let leftItems = this.$refs.left.getElementsByTagName("li");
                            let elLi = leftItems[i];
                            this.lefts.scrollToElement(elLi);
                            return i;
                        }
                    }
                    // this.listHeight0
                    return 0;
                }
            },
    }
</script>

<style lang="scss" scoped>
h3{
    padding-left:10px;
    height: 80px;
    margin: 0;
    color: white;
    text-align: center;
    background-color: tomato;
}
  *{
            padding: 0;
            margin: 0;
        }
        ul{
            list-style: none;
        }
        .content{
            display: flex;
            position: absolute;
            top: 50px;
            bottom: 50px;
            width: 100%;
            background-color: #f5f5f5;
        }
        .left{
            flex: 0 0 100px;
            width: 100px;
            background-color: #f5f5f5;
            overflow: hidden;
            border-right: 2px solid #f5f5f5;
            li{
                &.current{
                    border-left:2px solid #ed2a17;
                    color: #ed2a17;
                }
                span{
                    &.left-item{
                        display: block;
                        width: 100%;
                        height: 60px;
                        line-height: 60px;         
                        text-align: center;
                    }
                }
            }
        }
        .right{
            flex:1;
            overflow: hidden;
            background-color: #f5f5f5;
            &>ul{
                height: 2800px;
                margin-bottom: 30px;
                background-color: #fff;
                li{
                    background-color: #fff;
                    h2{
                    line-height: 50px;
                    text-align: left;
                    padding-left: 10PX;
                    font-size: 20PX;
                    font-weight: bold;
                    }
                    ul{
                        li{
                            text-align: center;
                            float: left;
                            width: 90px;
                            height: 120px;
                            padding: 5px;
                            .imgli{
                                width: 100%;
                                height: 90px;
                                img{
                                    width: 90px;
                                    height: 90px;
                                }
                            }
                            
                        }
                    }
                }
            }
        }  
        .clearfix::after{
            content: "";
            display: block;
            clear: both;
        }
</style>